.main{
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: url("../image/bg.jpg");
	background-size: 100% 93vh;
	background-repeat: no-repeat;
	min-height: 85vh;
	.banner{
		position: relative;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 85vh;

		.banner__headline{
			width: 50%;
			text-align: center;
			font-size: 42px;
			margin-bottom: 30px;
			color: #ffffff;
			z-index: 10;
		}
		.banner__btn{
			padding: 20px 30px;
			border: none;
			font-weight: 700;
			border-radius: 5px;
			background-color: #16739c;
			color: #ffffff;
			z-index: 10;
			cursor: pointer;
			transition: 0.3s;
		}
		.banner__btn:hover{
			background-color: #254c8a;
		}
	}
	.banner:after{
		content: "";
		position: absolute;
		height: 300px;
		width: 800px;
		background-color: rgba(1,1,1, 0.5);
	}
}
/*-------------------------------------------------------------------------------------------------
																										About
-------------------------------------------------------------------------------------------------*/

.about{
	justify-content: space-between;
	flex-wrap: wrap;
	height: 85vh;
	padding: 50px 40px;
	color: #fff;
	background-color: rgba(1,1,1, 0.8);
	.about__headline{
		width: 100%;
		font-size: 42px;
		text-align: center;
		margin-bottom: 50px;
	}
	.about-item{
		display: flex;
		justify-content: center;
		width: 32%;
		height: 300px;
		padding: 20px 10px;
		.about-item__text{
			font-weight: 700;
			margin-top: auto;
			background-color: rgba(1,1,1, 0.5);
			color: white;
			padding: 20px;
		}
	}
		.about-item:hover .about-item__text{
			background-color:rgba(1,1,1, 0.8);
		}
	.bg-gj{
		background-image: url("../image/gj.jpg");
		background-size: 100% 300px;
	}
	.bg-document{
		background-image: url("../image/document.jpg");
		background-size: 100% 300px;
	}
	.bg-justice{
		background-image: url("../image/justice.jpg");
		background-size: 100% 300px;
	}
}
/*-------------------------------------------------------------------------------------------------
																										Services
-------------------------------------------------------------------------------------------------*/
.services{
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	height: 85vh;
	color: #fff;
	background-color: rgba(1,1,1,0.9);

	.services-tegline-wrapper{
		display: flex;
		flex-wrap: wrap;
		width: 40%;
		.services-tegline-wrapper__headline{
			font-size: 42px;
			margin-bottom: 20px;
		}
		.services-tegline-wrapper__text{
			font-size: 16px;
			margin-bottom: 30px;
      text-align: justify;
      font-weight: 400;
      line-height: 22px;
		}
	}
	.services-slider{
		display: flex;
		width: 45%;
		.services-slider-item{
			display: flex;
			align-items: center;
			height: 300px;
			width: 80%;
			margin: 0 20px;
			.services-slider-item__text{
				padding: 20px 0;
				width: 100%;
				text-align: center;
				font-weight: 600;
				background-color: rgba(1,1,1, 0.8);

			}
		}
		.bg-one{
			background-image: url("../image/bg-one.jpg");
			background-size: 100% 300px;
			background-repeat: no-repeat;
		}
		.bg-two{
			background-image: url("../image/bg-two.jpg");
			background-size: 100% 300px;
			background-repeat: no-repeat;
		}
		.bg-three{
			background-image: url("../image/bg-three.jpg");
			background-size: 100% 300px;
			background-repeat: no-repeat;
		}
	}
	.slider__btn{
		padding: 15px 20px;
		font-size: 26px;
		font-weight: 600;
		border-radius: 50%;
		margin: auto;
		cursor: pointer;
		background-color: rgba(1,1,1, 0.5);
		color: rgba(255,255,255, 0.5);
		border:2px solid rgba(255,255,255, 0.5);
		transition: 0.1s;
	}
	.slider__btn:hover{
		background-color: rgba(1,1,1, 0.1);
		color: white;
		border-color: rgba(255,255,255, 1);
	}
}
/*-------------------------------------------------------------------------------------------------
																										Contact
-------------------------------------------------------------------------------------------------*/
.contact{
		position: relative;
		flex-wrap: wrap;
		justify-content: center;
		color: #fff;

		.contact__headline{
			text-align: center;
			width: 100%;
			margin-bottom: 40px;
			z-index: 100;
		}
	&::after{
		content: "";
		position: absolute;
		width: 800px;
		height: 300px;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		margin: auto;
		background-color: rgba(1,1,1, 0.8);
		z-index: 0;
	}
}
.contact-item{
	z-index: 100;
	margin: 0 50px;
	&__headline{
		margin: 10px 0;
	}
	&__text{
		padding: 5px;
		font-weight: 500;
	}
	&__svg{
		width: 50px;
		height: 50px;
		margin-left: 60px;
		margin-bottom: 5px;
		fill: #fff;
	}
}
