body{
	font-family: 'Lato', sans-serif;
	height: 100vh;
}
	.fade {
    animation: 1s fade;
    animation-fill-mode: forwards;
	}
	.show{
		display: flex;

	}
	.hide{
		display: none;
	}

	@keyframes fade {
		0% {
		  opacity: 0;
		}
		100% {
		  opacity: 1;
		}
	}
@import "normalizeCSS.less";
@import "variables.less";
@import "blocks/header";
@import "blocks/main";
@import "blocks/footer";