.header{
	display: flex;
	align-items: center;
	height: 15vh;
	padding:0 40px;
	background-color: #262626;
	color: rgba(255,255,255, 0.9);
	.header__logo{
		margin-right: 10px;
	}
	.header__logo-wrapper{
		display: flex;
		flex-direction: column;
	}
	.header__nav{
		margin-left: auto;
		.header__menu{
			display: flex;
			.header__menu-item{
				margin-right: 50px;
				border-bottom: 2px solid transparent;
				padding-bottom: 5px;
				color: rgba(255,255,255, 0.9);
				font-weight: 900;
				font-size: 20px;
				cursor: pointer;
			}
			.header__menu-item:hover{
				border-bottom: 2px solid rgba(255,255,255, 0.9);
			}
		}
	}
}