.footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Lato', sans-serif;
  padding-top: 60px;
  color: #ffffff;
  background-color: #313131;
  .footer__headline{
    margin-bottom: 20px;
  }
  .footer__text{
    margin-bottom: 25px;
    font-size: 22px;
    color: rgba(255,255,255,0.5);
  }
  .footer__text-wrapper{
    display: flex;
    .footer__text--icon-fill{
      position: relative;
      font-size: 16px;
      margin-right: 60px;
    }
    .footer__text--icon-fill:after{
      content: "";
      position: absolute;
      width: 10px;
      height: 15px;
      top: 0px;
      left: -15px;
      background-image: url("../image/fill.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
    .footer__text--icon-phone{
      position: relative;
      font-size: 16px;
    }
    .footer__text--icon-phone:after{
      content: "";
      position: absolute;
      width: 15px;
      height: 15px;
      top: 0;
      left: -25px;
      background-image: url("../image/phone.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
  }
  .footer-social{
    margin-bottom:50px;
    .footer-social__item{
      margin-right: 5px;
      cursor: pointer;
    }
  }
  .footer__bottom-text{
    padding: 35px 0;
    width: 100%;
    text-align: center;
    background-color: #2c2c2c;
  }
}
